
.news-box{
    background:url(../../static/media/xia.e8486718.jpg) no-repeat;
    background-size:cover;
    background-attachment:fixed
}
.news-box .news-list{
    width:100%;
    overflow:hidden;
}
.news-box .statu-item{
    width:100%;
    overflow:hidden;
    padding:10px;
    background:rgba(0,0,0,.7);
    margin-bottom:10px;
    color:#fff;
}
.news-box .statu-item .statu-img{
    width:100%;
    display: flex;
    flex-wrap: wrap;
}
.news-box .statu-item .statu-img img{
    height:110px;
    width: auto;
    margin:0 10px 10px 0;
    cursor: pointer;
}
.news-box .btnGroup{
    width: 100%;
    padding:20px;
    text-align: center;
    overflow: hidden;
}
.news-box .btnGroup label{
    background:#fff;
    padding:4px 10px;
    border-radius:4px;
    margin:0 5px;
    color: #000;
}
.news-box .btnGroup button{
    padding:4px 10px;
    border-radius:4px;
    border:0;
    background:#ccc;
    cursor: pointer;
}
.turnlink{
    color:#3385FF;
}
.top{
    width:100%;
    padding:30px;
    overflow: hidden;
}
.search-inp{
    float: right;
}
.search-inp input{
    vertical-align: middle;
    width:160px;
    height:32px;
    border-radius:6px 0 0 6px;
    border:1px solid #ccc;
}
.search-inp button{
    vertical-align: middle;
    width:40px;
    height:32px;
    border-radius:0 6px 6px 0;
    border:1px solid #ccc;
    background: url(data:image/png;base64,) no-repeat center #ccc;
    background-size: 26px;
    cursor: pointer;
}
.layout{
    width: 100%;
    height:100%;
    background: rgba(0,0,0,.6);
    position:fixed;
    z-index: 999;
    left: 0;
    top: 0;
    overflow: auto;
}
.alert-box{
    width:40%;
    position:absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
    background:#fff;
    padding:32px;
    text-align: center;
    color: #333;
    border-radius:10px;
}
.alert-tip{
    width:40%;
    position:relative;
    left: 50%;
    top: 50%;
    line-height:26px;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
    background:#fff;
    padding:8px;
    text-align: center;
    color: #333;
    border-radius:6px;
}
.alert-box .closebtn{
    position: absolute;
    width:32px;
    height: 32px;
    right:-10px;
    top:-10px;
    border-radius:50%;
    background: url(data:image/png;base64,) no-repeat center #fff;
    background-size:18px;
    cursor: pointer;
}
.alert-box img{
    width: 100%;
    height: auto;
}
