.top{
    width:100%;
    padding:30px;
    overflow: hidden;
}
.search-inp{
    float: right;
}
.search-inp input{
    vertical-align: middle;
    width:160px;
    height:32px;
    -webkit-border-radius:6px;
    -moz-border-radius:6px;
    border-radius:6px 0 0 6px;
    border:1px solid #ccc;
}
.search-inp button{
    vertical-align: middle;
    width:40px;
    height:32px;
    border-radius:0 6px 6px 0;
    border:1px solid #ccc;
    background: url("./../../static/images/search.png") no-repeat center #ccc;
    background-size: 26px;
    cursor: pointer;
}